	module.exports = {"data":{"challengeNode":{"title":"Use CSS Grid units to Change the Size of Columns and Rows","description":["在 CSS 网格中，可以使用绝对定位和相对定位单位如<code>px</code>和<code>em</code>来确定行或列的大小。下面的单位也可以使用：","<code>fr</code>：设置列或行占剩余空间的一个比例，","<code>auto</code>：设置列宽或行高自动等于它的内容的宽度或高度，","<code>%</code>：将列或行调整为它的容器宽度或高度的百分比，","最右侧的预览区中的效果通过下面的代码实现：","<blockquote>grid-template-columns: auto 50px 10% 2fr 1fr;</blockquote>","这段代添加了五个列。第一列的宽与它的内容宽度相等；第二列宽 50px；第三列宽是它容器的 10%；最后两列，将剩余的宽度平均分成三份，第四列占两份，第五列占一份。","<hr>","生成一个包含三列的网格，每列宽度分别为：1fr，100px，和 2fr。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/css-grid/use-css-grid-units-to-change-the-size-of-columns-and-rows","blockName":"CSS Grid","tests":[{"text":"<code>container</code>类应该有<code>grid-template-columns</code>属性，且生成宽为<code>1fr, 100px</code>和<code>2fr</code>的三列。","testString":"assert(code.match(/.container\\s*?{[\\s\\S]*grid-template-columns\\s*?:\\s*?1fr\\s*?100px\\s*?2fr\\s*?;[\\s\\S]*}/gi), '<code>container</code>类应该有<code>grid-template-columns</code>属性，且生成宽为<code>1fr, 100px</code>和<code>2fr</code>的三列。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  .d1{background:LightSkyBlue;}\n  .d2{background:LightSalmon;}\n  .d3{background:PaleTurquoise;}\n  .d4{background:LightPink;}\n  .d5{background:PaleGreen;}\n  \n  .container {\n    font-size: 40px;\n    width: 100%;\n    background: LightGray;\n    display: grid;\n    /* 请修改本行以下的代码 */\n    \n    grid-template-columns: auto 50px 10% 2fr 1fr;\n    \n    /* 请修改本行以上的代码 */\n    grid-template-rows: 50px 50px;\n  }\n</style>\n  \n<div class=\"container\">\n  <div class=\"d1\">1</div>\n  <div class=\"d2\">2</div>\n  <div class=\"d3\">3</div>\n  <div class=\"d4\">4</div>\n  <div class=\"d5\">5</div>\n</div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/css-grid/create-a-column-gap-using-grid-column-gap","id":"5a9036ee38fddaf9a66b5d34"},"slug":"/responsive-web-design/css-grid/use-css-grid-units-to-change-the-size-of-columns-and-rows"}}

